import React, { useState } from 'react';
import { Tabs } from '@sinohealth/butterfly-ui-antd/lib';
import ChartOrderAmount from './components/chartOrderAmount/index';
import OrderForm from './components/orderForm/index';
import RevenueDetails from './components/revenueDetails/index';
import SalesRanking from './components/salesRanking/index';
import { formatToDate } from '@/utils/utils';
import styles from './index.less';
import {
  httpGetQueryTotalStatisticsInfo,
  httpGetQueryCompleteAmountTrend,
  httpGetQueryOrderQuantityTrend,
  httpGetDrugSaleTopTenList,
} from '@/services/system';
import { revenueDetaiData } from './config';

export default function Operation() {
  const { TabPane } = Tabs;
  // 各个汇总的数据
  const [prescriptionCountData, setprescriptionCountData] = useState<any>(revenueDetaiData);
  // 获取各个汇总的数据参数
  const [queryParams, setQueryParamsParams] = useState<any>({
    beginDate: '', // getTimeFrame('最近 1 天')[0],
    endDate: '', // getTimeFrame('最近 1 天')[1],
    sameDay: '',
    storeIds: [],
  });
  const gettems = async (data: any) => {
    setQueryParamsParams(data);
    // tabs订单金额
    getQueryTotalStatisticsInfo(data);
    if (currentKey === '1') getOrderAmount(data);
    if (currentKey === '2') getOrderData(data);
    // callback(currentKey); // getOrderAmount();
    // 药品-销量金额前十排行榜
    getdrugSaleTopTenList(data);
  };
  // 获取各个汇总的数据
  const getQueryTotalStatisticsInfo = async (data: any) => {
    const res: any = await httpGetQueryTotalStatisticsInfo(data);
    setprescriptionCountData(res);
  };
  const [currentKey, setCurrentKey] = useState<string>('1');
  // tabs订单金额 || tabs订单数据
  const [OrderAmount, setOrderAmount] = useState<any>({
    data: [],
    padding: [50, 50, 30, 80],
    xField: 'time',
    yField: 'value',
    seriesField: 'date',
    yAxis: {
      label: {
        // 数值格式化为千分位
        formatter: (v: any) => `${v}`.replace(/\d{1,3}(?=(\d{3})+$)/g, (s) => `${s},`),
      },
    },
  });
  // 切换tabs
  const callback = async (e: any) => {
    setCurrentKey(e);
    if (e === '1') await getOrderAmount(queryParams);
    if (e === '2') await getOrderData(queryParams);
  };
  // tabs订单金额
  const getOrderAmount = async (data: any) => {
    const { sameDay, ...params } = data;
    const res: any = await httpGetQueryCompleteAmountTrend(params);
    const Arr: any = [];
    res.forEach((item: any) => {
      // isExhibition === false 只显示总金额
      if (item.isExhibition) {
        Object.keys(item).forEach((ele) => {
          let lsCategory: string = '';
          if (ele === 'dateTime' || ele === 'isExhibition') {
            return;
          }
          if (ele === 'b2cDataList') {
            lsCategory = 'B2C 渠道收入金额';
          } else if (ele === 'dataItems') {
            lsCategory = '收入总金额';
          } else if (ele === 'o2oDataList') {
            lsCategory = 'O2O 渠道收入金额';
          }
          const obj: any = {
            category: lsCategory,
            value: '',
            date: lsCategory,
            time: item?.dateTime?.includes(' ')
              ? `${item?.dateTime?.split(' ')[1]}:00`
              : item?.dateTime,
          };
          if (item[ele].length !== 0) {
            item[ele].forEach((element: any) => {
              obj.value = element.completeAmountDe;
              Arr.push(obj);
            });
          } else {
            obj.value = 0;
            Arr.push(obj);
          }
        });
      } else {
        const obj: any = {
          category: '收入总金额',
          value: '',
          date: '收入总金额',
          time: item?.dateTime?.includes(' ')
            ? `${item?.dateTime?.split(' ')[1]}:00`
            : item?.dateTime,
        };
        if (item.dataItems.length !== 0) {
          item.dataItems.forEach((element: any) => {
            obj.value = element.completeAmountDe;
            Arr.push(obj);
          });
        } else {
          obj.value = 0;
          Arr.push(obj);
        }
      }
    });
    console.log(Arr);
    setOrderAmount({ data: Arr });
  };
  // tabs订单数据
  const getOrderData = async (data: any) => {
    const res: any = await httpGetQueryOrderQuantityTrend(data);
    const Arr: any = [];
    res.forEach((item: any) => {
      Object.keys(item).forEach((ele) => {
        let lsCategory: string = '';
        if (ele === 'totalQuantity' || ele === 'completeQuantity') {
          if (ele === 'totalQuantity') lsCategory = '订单数量';
          if (ele === 'completeQuantity') lsCategory = '成交订单';
          const obj: any = {
            category: lsCategory,
            value: item[ele] * 1,
            date: lsCategory,
            time: item?.dateTime?.includes(' ')
              ? `${item?.dateTime?.split(' ')[1]}:00`
              : item?.dateTime,
          };
          Arr.push(obj);
        }
      });
    });
    setOrderAmount({ data: Arr });
  };
  // 药品-销量金额前十排行榜
  const [drugSaleTopTenList, setDrugSaleTopTenList] = useState<any>({});
  // 药品-销量金额前十排行榜
  const getdrugSaleTopTenList = async (data: any) => {
    const res: any = await httpGetDrugSaleTopTenList(data);
    setDrugSaleTopTenList(res);
  };

  return (
    <div>
      <OrderForm
        transmission={(entity: any) => {
          gettems(entity);
        }}
        clas={styles.mb12}
      />
      <div>
        <RevenueDetails revenueDeta={prescriptionCountData} />
        <div className={`${styles['bg-W']} ${styles.padd10} ${styles.mb12}`}>
          <Tabs defaultActiveKey={currentKey} onChange={callback}>
            <TabPane tab="订单金额" key="1" />
            <TabPane tab="订单数据" key="2" />
          </Tabs>
          <ChartOrderAmount OrderAmount={OrderAmount} />
        </div>
        <SalesRanking clas={styles.mb12} drugList={drugSaleTopTenList} />
      </div>
    </div>
  );
}
