import React, { useEffect, useState } from 'react';
import { Popover } from '@sinohealth/butterfly-ui-antd/lib';
import styles from './index.less';
import PopCon from '../popCon/index';
import systemItem from '@/assets/images/system/systemItem.svg';
import systemNum from '@/assets/images/system/systemNum.svg';
import systemOrder from '@/assets/images/system/systemOrder.svg';

type RevenueDetailsProps = {
  revenueDeta: any; // AdminOrder.RevenueDetailsList;
};

/**
 * 经营状况-收益详情
 * @returns
 */
const OrderStatus: React.FC<RevenueDetailsProps> = (props) => {
  const { revenueDeta } = props;
  const [prescriptionCountData, setPrescriptionCountData] = useState<any>([]);
  // 渲染各个汇总的数据
  const geetPrescriptionCountData = () => {
    setPrescriptionCountData([
      {
        icon: <img src="" />,
        ifPres: true,
        title: '收入总金额',
        total: revenueDeta.completeAmountElement,
        label: '累计收入总金额',
        number: revenueDeta.cumulativeCompleteAmountElement,
        son: {
          title: '处方总金额',
          total: revenueDeta.prescriptionAmountElement,
          label: '累计处方总金额',
          numbers: revenueDeta.cumulativePrescriptionElement,
        },
        bgClass: 'bg-yellow',
      },
      {
        icon: <img src={systemOrder} />,
        ifPres: false,
        title: '完成订单量 (张)',
        total: revenueDeta.completeQuantity,
        label: '累计完成订单量',
        number: revenueDeta.cumulativeCompleteQuantity,
        bgClass: 'bg-order',
      },
      {
        icon: <img src={systemItem} />,
        ifPres: false,
        title: '服务患者人次 (人次)',
        total: revenueDeta.orderQuantity,
        label: '累计服务患者人次',
        number: revenueDeta.cumulativeServingPatients,
        bgClass: 'bg-service',
      },
      {
        icon: <img src={systemNum} />,
        ifPres: false,
        title: '新增用户数 (人)',
        total: revenueDeta.newPatient,
        label: '累计用户数',
        number: revenueDeta.cumulativeNewUser,
        bgClass: 'bg-add',
      },
    ]);
  };
  // 初始化
  useEffect(() => {
    // 渲染各个汇总的数据
    geetPrescriptionCountData();
  }, [revenueDeta]);
  const PopoverDom = (pro: any) => (
    <Popover
      content={<PopCon pros={pro} />}
      placement="right"
      overlayClassName={styles['header-prop']}
      trigger="hover"
    >
      <span className={styles['header-right']}>处方</span>
    </Popover>
  );
  return (
    <div className={`${styles['revenue-con']}`}>
      <ul className={styles['revenue-view']}>
        {prescriptionCountData.map((item: any) => (
          <li key={item.title} className={`${styles['revenue-list']} ${styles[item.bgClass]}`}>
            <div className={styles['list-header']}>
              <span className={styles['header-left']}>{item.title}</span>
              {item.ifPres ? <PopoverDom pro={item} /> : null}
            </div>
            <div className={styles['list-money']}>
              <div>{item.total}</div>
              <div>{item.icon}</div>
            </div>
            <div className={styles['list-total']}>
              {item.label}
              <span className={styles['total-money']}>{item.number}</span>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default OrderStatus;
