import React, { useEffect, useState } from 'react';
import styles from './index.less';

type RevenueDetailsProps = {
  pros: any;
};

/**
 * 经营状况-收益详情
 * @returns
 */
const PopCon: React.FC<RevenueDetailsProps> = (props) => {
  const { pros } = props;
  const [lsPros, setPros] = useState<any>({
    title: '',
    total: 0,
    label: '',
    numbers: 0,
  });
  useEffect(() => {
    console.log(pros.pro);
    setPros(pros.pro.son);
  }, [pros]);

  return (
    <div className={styles['prop-con']}>
      <div className={styles['prop-title']}>
        <span className={styles['prop-line']} />
        <span className={styles['prop-text']}>处方</span>
      </div>
      <div className={styles['prop-bottom']}>
        <p className={styles['prop-list']}>
          <span className={styles['prop-label']}>{lsPros.title}</span>
          <span className={styles['prop-money']}>{lsPros.total}</span>
        </p>
        <p className={styles['prop-list']}>
          <span className={styles['prop-label']}>{lsPros.label}</span>
          <span className={styles['prop-money']}>{lsPros.numbers}</span>
        </p>
      </div>
    </div>
  );
};

export default PopCon;
