import React from 'react';
import SalesRankingTable from '@/components/TableBox';
import styles from './index.less';

type salesRankingProps = {
  clas: any;
  drugList: any;
};
/**
 * 经营状况-销售排行
 * @returns
 */
const SalesRanking: React.FC<salesRankingProps> = (props) => {
  const { clas, drugList } = props;
  const drugColumns: any = [
    {
      title: '排名',
      align: 'center',
      width: 70,
      render: (text: any, record: any, index: any) => {
        let span: any = '';
        if (index + 1 <= 3) {
          span = (
            <div key={`${index + text}`} className={`${styles['sales-ranking']}`}>
              <span className={`${styles['sales-ranking-list']} ${styles['sales-ranking-font']}`}>
                {`${index + 1}`}
              </span>
            </div>
          );
        } else {
          span = (
            <span
              key={`${index + text}`}
              className={`${styles['sales-ranking-other']} ${styles['sales-ranking-font']}`}
            >
              {`${index + 1}`}
            </span>
          );
        }
        return span;
      },
    },
    {
      title: '药品名称',
      dataIndex: 'drugName',
      key: 'drugName',
      align: 'center',
    },
    {
      title: '销售数量',
      dataIndex: 'saleQuantity',
      key: 'saleQuantity',
      align: 'center',
      width: 100,
    },
    {
      title: '药品规格',
      dataIndex: 'spec',
      key: 'spec',
      align: 'center',
      width: 100,
    },
    {
      title: '单位',
      dataIndex: 'packageUnit',
      key: 'packageUnit',
      align: 'center',
      width: 80,
    },
    {
      title: '企业名称',
      dataIndex: 'companyName',
      key: 'companyName',
      align: 'right',
      ellipsis: true,
    },
  ];

  const orgColumns: any = [
    {
      title: '排名',
      align: 'center',
      width: 70,
      render: (text: any, record: any, index: any) => {
        let span: any = '';
        if (index + 1 <= 3) {
          span = (
            <div key={`${index + text}`} className={`${styles['sales-ranking']}`}>
              <span className={`${styles['sales-ranking-list']} ${styles['sales-ranking-font']}`}>
                {`${index + 1}`}
              </span>
            </div>
          );
        } else {
          span = (
            <span
              key={`${index + text}`}
              className={`${styles['sales-ranking-other']} ${styles['sales-ranking-font']}`}
            >
              {`${index + 1}`}
            </span>
          );
        }
        return span;
      },
    },
    {
      title: '药品名称',
      dataIndex: 'drugName',
      key: 'drugName',
      align: 'center',
    },
    {
      title: '销售金额',
      dataIndex: 'saleAmountElement',
      key: 'saleAmountElement',
      align: 'center',
      width: 100,
    },
    {
      title: '药品规格',
      dataIndex: 'spec',
      key: 'spec',
      align: 'center',
      width: 100,
    },
    {
      title: '单位',
      dataIndex: 'packageUnit',
      key: 'packageUnit',
      align: 'center',
      width: 80,
    },
    {
      title: '企业名称',
      dataIndex: 'companyName',
      key: 'companyName',
      align: 'right',
      ellipsis: true,
    },
  ];

  return (
    <div className={`${styles['sales-con']} ${clas}`}>
      <div className={`${styles['sales-right']} ${styles['sales-view']}`}>
        <SalesRankingTable
          columns={drugColumns}
          dataSource={drugList.salesNumberList}
          title="药品销售数量TOP10"
        />
      </div>
      <div className={`${styles['sales-right']} ${styles['sales-view']}`}>
        <SalesRankingTable
          columns={orgColumns}
          dataSource={drugList.salesRankingList}
          title="药品销售金额TOP10"
        />
      </div>
    </div>
  );
};

export default SalesRanking;
