import React, { useState, useEffect } from 'react';
import { Form, Button, Radio, DatePicker, Select } from '@sinohealth/butterfly-ui-antd/lib';
import { useLocation } from 'react-router-dom';
import moment from 'moment';
import { useRecoilValue } from 'recoil';
import { userInfoAtom } from '@/store/atom';
import operation from './index.less';
import { httpGetListAllChain } from '@/services/system';
import { formatToDate, getTimeFrame } from '@/utils/utils';

const { Option } = Select;

type OrderFormProps = {
  clas: any;
  // 调父组件方法
  transmission: (values: any) => void;
};
/**
 * 经营状况查询条件
 * @returns
 */
const OrderForm: React.FC<OrderFormProps> = (props) => {
  const { clas, transmission } = props;
  const useRecoData = useRecoilValue(userInfoAtom);
  const { RangePicker } = DatePicker;
  const [form] = Form.useForm();
  const location = useLocation();
  const querys = async () => {
    if (!useRecoData.chainId) return;
    const fieldsValue: any = await form.getFieldsValue();
    let chainId: any = [];
    if (location.pathname === '/system/order/operation') {
      chainId = fieldsValue.selectdDte;
    } else {
      chainId[0] = useRecoData.chainId;
    }
    const date: any = fieldsValue.radio
      ? getTimeFrame(fieldsValue.radio)
      : [formatToDate(fieldsValue.date[0]), formatToDate(fieldsValue.date[1])];
    const obj: any = {
      beginDate: date[0],
      endDate: date[1],
      sameDay: '',
      storeIds: chainId,
    };
    Object.keys(obj).forEach((el) => {
      if (obj[el] === undefined || obj[el] === null || obj[el] === '') {
        obj[el] = '';
      }
    });

    transmission(obj);
  };

  // 订单来源
  const [optionsArr, setOptionsArr] = useState<any>([]);
  // 订单来源
  const getListAllChain = async () => {
    const res: any = await httpGetListAllChain('zmn-rx-oms-server');
    setOptionsArr(
      res?.map((item: any) => (
        <Select.Option value={item.id} title={item.name} key={item.id}>
          {item.name}
        </Select.Option>
      )),
    );
  };

  const reset = () => {
    form.resetFields();
  };

  useEffect(() => {
    // 获取订单来源
    getListAllChain();
  }, []);
  const getQuerys = () => {
    if (!useRecoData.chainId) return;
    querys();
  };
  useEffect(() => {
    getQuerys();
  }, [useRecoData]);

  useEffect(() => {
    getQuerys();
  }, [location.pathname]);

  // 改变时间范围
  const onChange = (e: any) => {
    form.setFieldsValue({ date: [] });
  };
  const anger: string[] = ['最近 1 天', '最近 1 周', '最近 1 月', '最近半年', '最近 1 年'];
  const onChangePicker = (e: any) => {
    if (e) {
      form.setFieldsValue({ radio: '' });
    } else {
      form.setFieldsValue({ radio: '最近 1 天' });
    }
  };
  const disabledTaskDate = (current: any) => current && current > moment().endOf('day'); // 当天以后的时间不能选择
  return (
    <div className={`${operation.conBox} ${clas}`}>
      <div className={operation.queryCon}>
        <Form layout="inline" form={form}>
          <Form.Item
            className={`${operation.distance} ${operation.mR16}`}
            label="时间范围"
            name="radio"
            initialValue="最近 1 天"
          >
            <Radio.Group onChange={onChange} optionType="button" options={anger} />
          </Form.Item>
          <Form.Item name="date" label="" style={{ width: '260px' }}>
            <RangePicker
              placeholder={['开始日期', '结束日期']}
              onChange={onChangePicker}
              disabledDate={disabledTaskDate}
            />
          </Form.Item>
          <br />
          {location.pathname === '/system/order/operation' ? (
            <div>
              <Form.Item name="selectdDte" className={operation.distance} label="订单来源">
                <Select
                  mode="multiple"
                  style={{ width: 400 }}
                  placeholder="请选择"
                  filterOption={(input, option: any) =>
                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {optionsArr}
                </Select>
              </Form.Item>
            </div>
          ) : null}
        </Form>
      </div>
      <div className={operation.queryBtn}>
        <Button className={operation.mR16} type="primary" htmlType="submit" onClick={querys}>
          查询
        </Button>
        <Button onClick={reset}>重置</Button>
      </div>
    </div>
  );
};

export default OrderForm;
